body {
  margin: 0;
  padding: 0;
  background: #F3F8FB;
}
.Container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
.Container__Inner {
  background: white;
  border-radius: 5px;
  overflow: hidden;
  min-width: 450px;
  position: relative;
}
.Container__Inner header {
  padding: 40px;
  background: #44566E;
  color: white;
  text-align: center;
  position: relative;
  z-index: 2;
}
.Container__Inner header h3 {
  font-size: 40px;
  font-family: 'bariolregular';
  font-weight: bold;
}
.Container__Inner .error {
  width: 100%;
  background: #DC3253;
  color: white;
  padding: 5px;
  font-size: 20px;
  text-align: center;
  z-index: 1;
  position: absolute;
  top: 94px;
  transition: all 0.3s ease 0s;
}
.Container__Inner .activeError {
  transform: translateY(33px);
}
.Container__Inner form {
  padding: 50px 30px;
  font-family: 'Linotte-SemiBold';
}
.Container__Inner form .marginBottom {
  margin-bottom: 40px;
}
.Container__Inner form .Container__InputContainer {
  position: relative;
  box-sizing: border-box;
}
.Container__Inner form .Container__InputContainer label {
  color: lightgray;
  position: absolute;
  top: 8px;
  left: 0.3rem;
  pointer-events: none;
  transition: all 0.3s ease 0s;
}
.Container__Inner form .Container__InputContainer input {
  width: 100%;
  box-sizing: border-box;
  border-radius: 0;
  border: 0;
  border-bottom: 1px solid lightgray;
  padding: 5px 0 5px 5px;
  font-size: 16px;
  transition: all 0.3s ease 0s;
}
.Container__Inner form .Container__InputContainer .Container__StrengthMeterContainer {
  width: 100%;
  height: 4px;
  box-sizing: border-box;
}
.Container__Inner form .Container__InputContainer .Container__StrengthMeterContainer .StrengthMeter {
  height: 100%;
  transition: all 0.3s ease 0s;
}
.Container__Inner form .Container__InputContainer p {
  margin: 5px 0 0;
  color: gray;
}
.Container__Inner form .inputActive label {
  top: -15px;
  color: #44566E;
}
.Container__Inner form .inputActive input {
  border-bottom: 1px solid black;
}
.Container__Inner form input[type="checkbox"] {
  margin: 10px 0 50px;
}
.Container__Inner form .Container__Submit button {
  width: 100%;
  height: 50px;
  border: none;
  background: #fff;
  color: #585b5f;
  border-radius: 40px;
  text-transform: uppercase;
  letter-spacing: 0;
  font-weight: 600;
  font-size: 12px;
  box-shadow: 0 0 22px rgba(0, 0, 0, 0.07);
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  cursor: pointer;
}
.Container__Inner form .Container__Submit button:hover {
  background: #44566E;
  color: white;
}
.Container__Inner form .Container__Submit .Container__OtherLogin {
  margin-top: 20px;
  display: flex;
}
.Container__Inner form .Container__Submit .Container__OtherLogin .Container__OtherButton {
  width: 50%;
  padding: 10px;
}
.Container__Inner form .Container__Submit .Container__OtherLogin .Container__OtherButton .blue {
  background: #59819E;
  color: white;
}
.Container__Inner form .Container__Submit .Container__OtherLogin .Container__OtherButton .blue:hover {
  box-shadow: 3px 3px 10px #235172;
}
.Container__Inner form .Container__Submit .Container__OtherLogin .Container__OtherButton .orange {
  color: white;
  background: #fb5757;
}
.Container__Inner form .Container__Submit .Container__OtherLogin .Container__OtherButton .orange:hover {
  box-shadow: 3px 3px 10px #fb5757;
}
.Container__Inner form .Container__Footer {
  margin-top: 40px;
  display: flex;
  justify-content: center;
}
.Container__Inner form .Container__Footer a {
  text-decoration: none;
  color: #44566E;
}
