.Container {
  width: 100%;
  max-width: 120rem;
  margin: 0 auto;
}
.Container header {
  display: flex;
  align-items: baseline;
  padding: 2rem 1rem;
}
.Container header .Container__OptionsContainer {
  margin-left: auto;
}
.Container header .Container__OptionsContainer .OptionsContainer__Button {
  padding: 1rem;
  cursor: pointer;
}
.Container header .Container__OptionsContainer .OptionsContainer__Button p {
  pointer-events: none;
}
